<template>
  <div v-if="!model"></div>
  <div v-else class="user-detail">
    <div class="user-avatar">
      <Avatar class="large inline" :id="model.avatarFileId" />
    </div>
    <UserStats :posts="postCount" :followers="followerCount" :following="followingCount"  />
    <div class = "user-desc">
      <h4>{{nickname}}</h4>
      <span v-html="bioText"></span>
    </div>
    <MyPanel />
  </div>
</template>

<script>
import Avatar from '@/components/Avatar'
import UserStats from '@/components/users/UserStats'
import MyPanel from '@/components/users/MyPanel'

export default {
  components: { Avatar , UserStats, MyPanel},

  props: {
    model: Object,
    postCount: Number,
    inline: Boolean,
    avatar: Boolean,
  },

  computed: {
    nickname: ({ model }) => model.displayName ?? model.userId,
    bioText: ({model})=>{
      return model.description?.replaceAll(/\n/g,'<br/>')
    },
    followerCount: ({ model = { userId: '' } }) => (
      1.1 * model.userId
        .split('')
        .reduce((acc, char) => acc + char.charCodeAt(0), 0)
    ),
    followingCount: ({ model = { displayName: '' } }) => (
      0.9 * model.displayName
        .split('')
        .reduce((acc, char) => acc + char.charCodeAt(0), 0)
    ),

  }
}
</script>

<style lang="stylus" scoped>
.user-detail
  padding 1.7rem 1.2rem
  display grid
  grid-template-columns auto auto auto auto
  grid-template-rows 6rem fit-content(9rem) 3.5rem
  grid-template-areas 'avatar stats stats stats'

  .user-desc
    grid-column 1/5
    h4
      margin-bottom 0.5rem

</style>

